.post {
  /* max-width: 500px; */
  width: 100%;
  background-color: white;
  border: 1px solid lightgray;
  margin-bottom: 45px;
}

@media (min-width: 1025px) {
  .post {
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    /* margin-left: 20%;
    margin-right: 20%; */
  }
}

.post__image {
  width: 100%;
  object-fit: contain;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}

.post__text {
  font-weight: normal;
  padding: 20px;
}

.post__header {
  display: flex;
  align-items: center;
  padding: 20px;
}

.post__avatar {
  margin-right: 10px;
}

.post__commentBox {
  display: flex;
  margin-top: 10px;
}
.post__input {
  flex: 1;
  border: none;
  padding: 10px;
  border-top: 1px solid lightgray;
}
.post__button {
  flex: 0;
  border: none;
  color: #6082a3;
  background-color: transparent;
  border-top: 1px solid lightgray;
}

.post__comments{
    padding: 20px;
}

@media (min-width: 480px) {
  .post__button {
    margin-right: 10px;
  }
  .post__commentBox {
    margin-right: 15px;
  }
}
